




































.tips-dialog-box {
  /deep/ .el-dialog {
    margin-top: 38vh !important;
  }
	/deep/ .el-dialog__header {
		padding: 0 80px 0 20px;
		border-bottom: 1px solid #eee;
		background: #4b77be;
		height: 30px;
		line-height: 30px;
	}
	/deep/ .el-dialog__title {
		font-size: 14px;
		color: #FFFFFF;
	}
	/deep/ .el-dialog__headerbtn {
		top: 6px;
	}
	/deep/ .el-dialog__headerbtn .el-dialog__close {
		color: #FFFFFF;
	}
	/deep/ .el-dialog__body {
		padding:0;
	}
	/deep/ .el-dialog__footer {
		border-top: 1px solid #e5e5e5;
  }
  .content-box {
		position: relative;
		.cancel-box {
			width: 20px;
			height: 20px;
			background: #4b77be;
			position: absolute;
			top: -25px;
			right: 20px;
			z-index: 20;
			color: #ffffff;
			font-size: 20px;
    }
    
    .text-box {
      padding: 15px 20px;
    }
	}
}
